import React from 'react';
import StarRatings from 'react-star-ratings';

const RatingStars = ({ rating }) => {
  // Round the rating to the nearest integer
  const roundedRating = Math.round(rating);

  // Calculate the percentage of the remaining star
  const decimalPart = rating - roundedRating;
  const percentage = decimalPart * 100;

  return (
    <div className="rating-stars">
      <StarRatings
        rating={roundedRating}
        starRatedColor="#FFBA5A"
        starEmptyColor="#e1e1e1"
        starDimension="30px"
        starSpacing="1px"
        numberOfStars={5}
        name="rating"
      />
      {decimalPart > 0 && (
        <span className="star-half" style={{ width: `${percentage}%` }}>
          &#189;
        </span>
      )}
    </div>
  );
};

export default RatingStars;
